.block {
  border-radius: 10px;
  background: rgba(123, 228, 149, 0.85);
  box-shadow: 0 6px 4px 0 rgba(0, 0, 0, 0.25);
  width: 243px;
  padding: 25px 51px;
  margin-top: clamp(0.938rem, -0.98vw + 1.176rem, 0rem);
  cursor: pointer;
}
.block .blockTitle {
  text-align: center;
  font-family: "PoppinsMedium", serif;
  font-weight: 500;
  font-size: 24px;
}
.block img {
  height: 161px;
  width: 161px;
}


